:root {
	--color-bg-dark: #f5f5f5;
	--color-bg: #fdfffd;
	--color-grey: #d1d1d1;
	--color-text: #22243b;
	--color-accent: #f16e03;
	--color-white: white;
	--color-scrollbar: #aaa7a7;
}
html.dark {
	--color-bg-dark: #1a1c35;
	--color-bg: #22243b;
	--color-grey: #4e4e4e;
	--color-text: #fdfffd;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	accent-color: var(--color-accent);
	font-size: 1.2rem;
	background: rgb(2, 0, 36);
	background: linear-gradient(
		90deg,
		rgba(2, 0, 36, 1) 0%,
		rgba(14, 14, 115, 1) 41%,
		rgba(20, 135, 158, 1) 100%
	);
}

#root {
	width: 100%;
	height: 60%;
	max-width: 500px;
	overflow: hidden;
	background-color: var(--color-bg-dark);
	border-radius: 1rem;
	display: flex;
	flex-direction: column;
	box-shadow: 12px 10px 31px 2px rgba(0, 0, 0, 0.58);
	-webkit-box-shadow: 12px 10px 31px 2px rgba(0, 0, 0, 0.58);
	-moz-box-shadow: 12px 10px 31px 2px rgba(0, 0, 0, 0.58);
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

* {
	box-sizing: border-box;
}

ul,
li {
	list-style: none;
	padding-left: 0;
}
button {
	outline: none;
	border: none;
}
::-webkit-scrollbar {
	width: 0.5rem;
}
::-webkit-scrollbar-track {
	background-color: var(--color-bg-dark);
}
::-webkit-scrollbar-thumb {
	background-color: var(--color-scrollbar);
}
::-webkit-scrollbar-thumb:hover {
	background-color: var(--color-accent);
}
